body{margin:0;padding:0;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif}.App{height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column}.quote-box{padding:10px;width:60vw;height:60vh;background-color:#e9e9e9;border-radius:5px;text-align:center}.developer{color:#e9e9e9}.developer a{color:#e9e9e9}.developer a:hover{color:#e9e9e9}.text{display:flex;justify-content:center;align-items:center;font-size:1.5rem;height:40vh;color:#000;quotes:"«" "»"}.author{height:10vh;text-align:right;margin-right:30px;color:#000;font-style:italic}.button-container{height:8vh;display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-left:10px;margin-right:10px}